<template>
    <div class="container mr-auto">
        <div class="jumbotron text-center">
            <h1>//</h1>
        </div>
        <hr>

        <div class="container">
            <form>
                <div class="form-group row">
                    <label for="text" class="col-2-1 col-form-label"></label>
                    <div class="col-sm-4">
                        <input type="text" class="form-control" id="exampleFormControlInput1" v-model="productPk">
                    </div>
                    <label for="text" class="col-sm-2-1
                    col-form-label"></label>
                    <div class="col-sm-5">
                        <input type="text" class="form-control" id="exampleFormControlInput1" v-model="productStore">
                    </div>
                </div>

                <div class="form-group row">
                    <label for="text" class="col-2-1 col-form-label"></label>
                    <div class="col-sm-4">
                        <select class="custom-select mr-sm-2" id="inlineFormCustomSelect" v-model="productCategoryPk">
                        <!-- <option selected > </option> -->
                        <!-- <option v-bind:value="{category: p1}"></option>
                        <option v-bind:value="{category: p2}"></option>
                        <option v-bind:value="{category: p3}"></option>
                        <option v-bind:value="{category: p4}"></option>
                        <option v-bind:value="{category: p5}"></option> -->
                        <option value="p1"></option>
                        <option value="p2"></option>
                        <option value="p3"></option>
                        <option value="p4"></option>
                        <option value="p5"></option>
                        </select>
                    </div>
                    
                    <label for="text" class="col-sm-2-1
                    col-form-label"></label>
                    <div class="col-sm-5">
                        <div class="input-group">
                            <input type="text" class="form-control" id="exampleFormControlInput1"  v-model="productPrice">
                            <div class="input-group-append">
                            <span class="input-group-text"></span>
                            </div>
                        </div>
                    </div>
                </div>

                <div class="form-group row">
                    <label for="text" class="col-sm-2-1
                    col-form-label"></label>
                    <div class="col-sm-4">
                        <input type="text" class="form-control" id="exampleFormControlInput1"  v-model="productStockCnt">
                    </div>

                    <div class="form-group row">
                    <label for="text" class="col-sm-3
                        col-form-label"></label>
                    <input type="file" class="form-control-file col" id="exampleFormControlFile1">
                    </div>
                </div>


                <div class="form-group row">
                    <label for="text" class="col-2-1 col-form-label">
                    </label>
                    <div class="col-sm-10">
                        <input type="text" class="form-control" id="exampleFormControlInput1" placeholder=" "  v-model="productName">
                    </div>
                </div>

                <div class="form-group row">
                    <label for="text" class="col-sm-2-1 col-form-label"></label>
                    <div class="col-sm-10">
                        <textarea class="form-control" id="exampleFormControlTextarea1" rows="8"  v-model="productDetail"></textarea>
                    </div>
                </div>

                <div class="form-group row">
                <label for="text" class="col-sm-2 col-form-label"></label>
                <input type="file" class="form-control-file col" id="exampleFormControlFile1">
                </div>

                <br>

                <div class="row">
                    <button type="button" class="btn btn-primary col-sm-2 ml-auto" id="btnmodi" v-on:click="moveProductStockList"></button>
                    <button type="button" class="btn btn-warning col-sm-2" id="btnmodi" v-on:click="ProductInsert"></button>
                    <!-- <button type="button" class="btn btn-danger col-sm-2"></button> -->
                </div>
            </form> 
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            productPk:'',
            productCategoryPk:'',
            productName:'',
            productPrice:'',
            productStockCnt:'',
            productStore:'',
            productDetail:'',
        }
    },
    methods: {
        //axios   
        ProductInsert() {
            let obj = this;
            obj.$axios.post('http://localhost:9000/productInsert', {
                
                productPk: this.productPk,
                productCategoryPk: this.productCategoryPk,
                productName: this.productName,
                productPrice: this.productPrice,
                productStockCnt: this.productStockCnt,
                productStore: this.productStore,
                productDetail: this.productDetail
            })
            .then(function() {
                console.log("  ");
                obj.$router.push({name:'AdminProductStock'});
                
            })
            .catch(function(err) {
                console.log("  ");
                console.log(err);
            })
        },
        //  
        moveProductStockList() {
            this.$router.push({ name: 'AdminProductStock'});
        }
    },
    
}
</script>

<style scoped>
#btnmodi {
    margin-right: 5px;
}

#center { display: inline-block;}
