<template>
    <div class="container">
        <div class="jumbotron text-center">
            <h1></h1>
        </div>
        <hr>

        <div class="container">
        <div class="col-md-12">
            <table class="table table-hover">
                <thead>
                    <tr>
                        <th>NO.</th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in items" v-bind:key="item.boardIdx">
                        <td>{{ item.boardIdx}}</td>
                        <td>{{ item.productPk}}</td>
                        <td>{{ item.productCategoryPk}}</td>
                        <td>{{ item.productName}}</td>
                        <td>{{ item.productStore}}</td>
                        <td>{{ item.productStockCnt}}</td>
                    </tr>
                </tbody>
            </table>

            <!--  -->
            <nav aria-label="Page navigation example">
                <ul class="pagination" style="justify-content: center;">
                    <li class="page-item">
                    <a class="page-link" href="#" aria-label="Previous">
                        <span aria-hidden="true">&laquo;</span>
                    </a>
                    </li>
                    <li class="page-item"><a class="page-link" href="#">1</a></li>
                    <li class="page-item"><a class="page-link" href="#">2</a></li>
                    <li class="page-item"><a class="page-link" href="#">3</a></li>
                    <li class="page-item">
                    <a class="page-link" href="#" aria-label="Next">
                        <span aria-hidden="true">&raquo;</span>
                    </a>
                    </li>
                </ul>
            </nav>

            <!--  -->
            <div class="row">
                    <button type="button" class="btn btn-dark col-sm-2 ml-auto" id="btnmodi" v-on:click="moveProductInsert"></button>
            </div>
        </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            items: []
        }
    },
    methods: {
        moveProductInsert() {
            this.$router.push({name: 'AdminProductInsert'});
        }
    },
    mounted() {
        let obj = this;

        obj.$axios.get("http://localhost:9000/stockList", {
            params: {
                boardIdx: 1
            }
        })
        .then(function(res) {
            console.log("axios   ");
            obj.items = res.data;
        })
        .catch(function(err) {
            console.log("axios   ");
            console.log(err);
        });
    }
}
</script>

