<template>
    <div class="container">

        <div class="jumbotron text-center">
            <h1></h1>
        </div>
        <hr>

        <div class="col-md-12">
            <table class="table table-hover">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in qnas" v-bind:key="item.qnaPk">
                        <td>{{ item.qnaPk}}</td>
                        <td>{{ item.productName }}</td>
                        <td>
                            <div data-toggle="collapse" v-bind:data-target="'#qna' + item.qnaPk" aria-expanded="false" v-bind:aria-controls="'qna' + item.qnaPk"> {{ item.qnaTitle }} </div>
                                <br>
                            <div class="collapse" v-bind:id="'qna' + item.qnaPk">
                            <div class="card card-body"> {{ item.qnaContents }} </div>
                                <div>
                                    <div class="card card-body">
                                        {{ item.answerContents }}
                                    </div>
                                </div>
                            </div>
                            </td>
                        <td>{{ item.createId}}</td>
                        <td>{{ item.createDate}}</td>
                        <td>{{ item.answerYn}}</td>
                        <td><button type="button" class="btn btn-dark" v-on:click="answerUpdate(item.qnaPk)"></button></td>
                    </tr>
                </tbody>
            </table>

            <!--  -->
            <nav aria-label="Page navigation example">
                <ul class="pagination" style="justify-content: center;">
                    <li class="page-item">
                    <a class="page-link" href="#" aria-label="Previous">
                        <span aria-hidden="true">&laquo;</span>
                    </a>
                    </li>
                    <li class="page-item"><a class="page-link" href="#">1</a></li>
                    <li class="page-item"><a class="page-link" href="#">2</a></li>
                    <li class="page-item"><a class="page-link" href="#">3</a></li>
                    <li class="page-item">
                    <a class="page-link" href="#" aria-label="Next">
                        <span aria-hidden="true">&raquo;</span>
                    </a>
                    </li>
                </ul>
            </nav>

        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            productName: "",
            qnas: []
        }
    },
    methods: {
        answerUpdate(qnaPk) {
            this.$router.push({ name: 'QnAWrite', query: { qnaPk: qnaPk }});
        },
    },
    mounted() {
        let obj = this;

        obj.$axios.get("http://localhost:9000/qnaList")
        .then(function(res) {
            console.log("axios   ");
            obj.qnas = res.data;
        })
        .catch(function(err) {
            console.log("axios   ");
            console.log(err);
        });
    },
    
}
</script>

