<template>
    <div class="container">

        <div class="jumbotron text-center">
            <h1></h1>
        </div>
        <hr>

        <div class="col-md-12">
            <table class="table table-hover">
                <thead>
                    <tr>
                        <th>NO.</th>
                        <th>SUBJECT</th>
                        <th>Name</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="item in items" v-bind:key="item.noticePk">
                        <td>{{ item.noticePk}}</td>
                        <td v-on:click="moveNoticeDetail(item.noticePk)">{{ item.noticeTitle}}</td>
                        <td>{{ item.createId}}</td>
                        <td>{{ item.createDate}}</td>
                    </tr>
                </tbody>
            </table>

            <!--  -->
            <nav aria-label="Page navigation example">
                <ul class="pagination" style="justify-content: center;">
                    <li class="page-item">
                    <a class="page-link" href="#" aria-label="Previous">
                        <span aria-hidden="true">&laquo;</span>
                    </a>
                    </li>
                    <li class="page-item"><a class="page-link" href="#">1</a></li>
                    <li class="page-item"><a class="page-link" href="#">2</a></li>
                    <li class="page-item"><a class="page-link" href="#">3</a></li>
                    <li class="page-item">
                    <a class="page-link" href="#" aria-label="Next">
                        <span aria-hidden="true">&raquo;</span>
                    </a>
                    </li>
                </ul>
            </nav>

            
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            items: []
        }
    },
    methods: {
        moveNoticeDetail(noticePk) {
            // push() - <router-link>     
            //      params    
            this.$router.push({ name: 'NoticeDetail', params: { noticePk: noticePk }});
        },
    },
    mounted() {
        let obj = this;

        obj.$axios.get("http://localhost:9000/noticeList")
        .then(function(res) {
            console.log("axios   ");
            obj.items = res.data;
        })
        .catch(function(err) {
            console.log("axios   ");
            console.log(err);
        });
    }
    
}
</script>

<style>
table {
    text-align: center;
}
</style>